import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { LoaderService } from '../loader.service';
import { DataService } from "../data.service";
import { MouseEvent, MapsAPILoader } from '@agm/core';
declare let google: any;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {
  @Output() messageEvent = new EventEmitter<string>();
  serverUrl: any = environment.baseUrl;
  previous;
  output: any = [];
  placeList: any = [];
  partners: any = [];
  liststatus: boolean = true;
  detstatus: boolean = false;
  markers = [] as any;
  icon: any = "../assets/images/blue-location.png";
  flag: any = "../assets/images/red-location.png"
  placedet: any = { photo: [{ img: "" }] };
  address: string;
  current_address: any = "";
  customOptions: OwlOptions = {
    loop: true,
    autoplay: false,
    mouseDrag: true,
    touchDrag: false,
    pullDrag: false,
    margin: 10,
    autoWidth: true,
    navText: ["", ""],
    dots: false,
    navSpeed: 700,
    responsive: {
      0: {
        items: 2
      },
      420: {
        items: 3
      },
      740: {
        items: 7
      },
      940: {
        items: 7
      }
    },
    nav: true
  }

  // google maps zoom level
  zoom: number = 12;

  // initial center position for the map
  // lat: number = 53.350140;
  // lng: number = -6.266155;
  lat: number;
  lng: number;

  changeActive: any = [];

  // clickedMarker(label: string, index: number) {
  //   console.log(`clicked the marker: ${label || index}`)
  // }

  // mapClicked($event: MouseEvent) {
  //   this.markers.push({
  //     lat: $event.coords.lat,
  //     lng: $event.coords.lng,
  //     draggable: true
  //   });
  // }

  markerDragEnd(m: marker, $event: MouseEvent) {
    console.log('dragEnd', m, $event);
  }

  // markers: marker[] = [
  //   {
  // 	  lat: 51.673858,
  // 	  lng: 7.815982,
  // 	  label: 'A',
  // 	  draggable: true
  //   },
  //   {
  // 	  lat: 51.373858,
  // 	  lng: 7.215982,
  // 	  label: 'B',
  // 	  draggable: false
  //   },
  //   {
  // 	  lat: 51.723858,
  // 	  lng: 7.895982,
  // 	  label: 'C',
  // 	  draggable: true
  //   }
  // ]
  constructor(public httpclient: HttpClient, private loaderService: LoaderService, private data: DataService) { }

  ngOnInit() {
    this.getLocation();
    this.loaderService.display(true);
    this.httpclient.get(this.serverUrl + 'category/api-category').subscribe(

      data => {
        this.loaderService.display(false);
        console.log(data);
        this.output = data;

      },

      error => {

        console.log("Rrror", error);

      }

    );;




    this.httpclient.get(this.serverUrl + 'partners').subscribe(

      datas => {
        this.partners = datas;
      },

      error => {

        console.log("Rrror", error);

      }

    );

    this.data.currentMessage.subscribe((message) => {
      console.log(message);
      this.previous = false;
      this.placeList = message;
      this.markers = [];
      if (this.placeList.length > 0) {
        //this.placeList.forEach((key : any, val: any) => {
        for (let i = 0; i < this.placeList.length; i++) {
          //console.log(key);
          this.markers.push({
            lat: this.placeList[i].lat,
            lng: this.placeList[i].lng,
            label: this.placeList[i].name,
            opening_hours: this.placeList[i].opening_hours,
            rating: this.placeList[i].rating,
            formatted_address: this.placeList[i].formatted_address,
            img: this.placeList[i].photo[0].img
          });
        }

        //});
      }
    })


  }

  google_map_directions(mylat, mylon) {
    window.open('https://www.google.com/maps/dir/' + mylat + ',' + mylon + '/' + this.lat + ',' + this.lng, '_system');

  }

  catdetails(cat_id, allcats) {
    var catall = allcats.filter(function (cats: any) {
      return cats._id == cat_id;
    });
    return (catall.length > 0 ? catall[0].name : "");
  }

  mapReady() {
    this.getAddress(this.lat, this.lng);
  }

  getAddress(latitude, longitude) {
    let geocoder = new google.maps.Geocoder();
    let latlng = new google.maps.LatLng(this.lat, this.lng);
    let request = {
      latLng: latlng
    };
    geocoder.geocode(request, (results, status) => {
      if (status == google.maps.GeocoderStatus.OK) {
        if (results[0] != null) {
          let city = results[0].address_components[results[0]
            .address_components.length - 4].short_name;
          console.log(results[0]);
          this.current_address = results[0].formatted_address;
          //this.messageEvent.emit(this.current_address);
          this.data.changehead(this.current_address);
          //this.shareService.setLocationDetails(city);
        } else {
          alert("No address available");
        }
      }
    });
  }

  clickToChange(index) {
    //alert(index)
    // this.changeActive = []
    this.changeActive[index] = true;

    // console.log(this.changeActive);
  }

  clickedMarker(infowindow) {
    if (this.previous) {
      this.previous.close();
    }
    this.previous = infowindow;
  }

  getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position: Position) => {
        if (position) {
          console.log(position);
          console.log("Latitude: " + position.coords.latitude +
            "Longitude: " + position.coords.longitude);
          this.lat = position.coords.latitude;
          this.lng = position.coords.longitude;
          this.getAddress(this.lat, this.lng);
          this.httpclient.post(this.serverUrl + 'placerecords', { distance: "20km", lat: this.lat, lng: this.lng, cat: [], place_id: '0' }).subscribe(

            datas => {
              this.loaderService.display(false);
              this.placeList = datas;
              this.markers = [];
              this.placeList.forEach((key: any, val: any) => {
                //console.log(key);
                this.markers.push({
                  lat: key.lat,
                  lng: key.lng,
                  label: key.name,
                  opening_hours: key.opening_hours,
                  rating: key.rating,
                  formatted_address: key.formatted_address,
                  img: key.photo[0].img
                });
              });
            },

            error => {

              console.log("Rrror", error);

            }

          );
        }
      },
        (error: PositionError) => {
          this.httpclient.post(this.serverUrl + 'placerecords', { distance: "20km", lat: this.lat, lng: this.lng, cat: [], place_id: '0' }).subscribe(

            datas => {
              this.loaderService.display(false);
              this.placeList = datas;
              this.markers = [];
              this.placeList.forEach((key: any, val: any) => {
                //console.log(key);
                this.markers.push({
                  lat: key.lat,
                  lng: key.lng,
                  label: key.name,
                  opening_hours: key.opening_hours,
                  rating: key.rating,
                  formatted_address: key.formatted_address,
                  img: key.photo[0].img
                });
              });
            },

            error => {

              console.log("Rrror", error);

            }

          );
        }, { maximumAge: 0, timeout: 5000, enableHighAccuracy: true });
    } else {
      alert("Geolocation is not supported by this browser.");
    }
  }

  placeDetails(list) {
    this.detstatus = true;
    this.liststatus = false;
    console.log(list);
    this.placedet = list;
  }

  close() {
    this.detstatus = false;
    this.liststatus = true;
  }

}

// just an interface for type safety.
interface marker {
  lat: number;
  lng: number;
  label?: string;
  draggable: boolean;
}