import { Component, OnInit, EventEmitter, Output, ViewChild, ElementRef } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { LoaderService } from '../../loader.service';
import { DataService } from "../../data.service";
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  serverUrl: any = environment.baseUrl;
  lat: number = 53.350140;
  lng: number = -6.266155;
  place_id:any = "0";
  
  @ViewChild('nameInput', {static: false}) nameInput: ElementRef;
  outputs: any = [];
  result:any = [];
  current_loc:any="";
  constructor(public httpclient: HttpClient, private loaderService: LoaderService,private data:DataService) { }

  onClickSubmit(data) {
    this.loaderService.display(true);
    data.distance = this.nameInput.nativeElement.value;
    console.log(data);
    console.log(this.lat);
    console.log(this.lng);
    this.httpclient.post(this.serverUrl + 'placerecords', {distance:data.distance,lat:this.lat,lng:this.lng,cat:data.fit_cat,place_id:this.place_id}).subscribe(

      datas => {
        this.loaderService.display(false);
        console.log(datas);
        this.result = datas;
        this.data.changeMessage(datas);
      },

      error => {

        console.log("Rrror", error);

      }

    );
  }

  

  getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position: Position) => {
        if (position) {
          console.log("Latitude: " + position.coords.latitude +
            "Longitude: " + position.coords.longitude);
          this.lat = position.coords.latitude;
          this.lng = position.coords.longitude;

        }
      },
        (error: PositionError) => console.log(error),{enableHighAccuracy:true});
    } else {
      alert("Geolocation is not supported by this browser.");
    }
  }

  handleAddressChange(e){
    console.log(e);
    this.place_id = e.name;
  }

  ngOnInit() {
    this.getLocation();
    this.data.currentMessages.subscribe((message) => {
      console.log(message);
      this.current_loc = message;
    })

    this.httpclient.get(this.serverUrl+'category/api-category').subscribe(

      data  => {
        this.loaderService.display(false);
      console.log("suman99",data);
      this.outputs = data;
      
      },
      
      error  => {
      
      console.log("Rrror", error);
      
      }
      
      );;
  }
}
